import { StatusBar } from 'expo-status-bar'
import { NativeBaseProvider, Center, Box, Text, Input, Button, Switch, HStack } from 'native-base'
import React, { useEffect, useState } from 'react'
import * as CryptoJS from 'crypto-js'
import * as Clipboard from 'expo-clipboard'

export default function App() {
	const [Texto, setTexto] = useState<string>(null)
	const secretKey = 'mozÃ£o'

	const Colar = async () => {
		const text = await Clipboard.getStringAsync()
		setTexto(text)
	}

	const Criptografar = () => {
		const ciphertext = CryptoJS.AES.encrypt(Texto, secretKey).toString()
		setTexto(ciphertext)
	}

	const Descriptografar = () => {
		const bytes = CryptoJS.AES.decrypt(Texto, secretKey)
		const decryptedText = bytes.toString(CryptoJS.enc.Utf8)

		setTexto(decryptedText)
	}

	const copyToClipboard = async () => {
		await Clipboard.setStringAsync(Texto)
		alert('Texto Copiado!')
	}

	useEffect(() => {
		Colar()
	}, [])

	return (
		<NativeBaseProvider>
			<Box bgColor={'cyan.400'} flex={1}>
				<Text alignSelf={'center'} fontWeight={'black'} color={'white'} fontSize={'5xl'} my={'5'}>
					Criptografia
				</Text>
				<Box bgColor={'cyan.900'} w={'container'} h={'full'} borderTopRadius={'3xl'} alignItems={'center'} pt={'5'}>
					<Input value={Texto} onChangeText={setTexto} variant="underlined" placeholder=" Mensagem" size="2xl" color={'white'} />

					<HStack space={'10'} my={'10'}>
						<Button onPress={Criptografar}>Criptografar</Button>
						<Button onPress={Descriptografar}>Descriptografar</Button>
					</HStack>
					<Button onPress={copyToClipboard}>Copiar</Button>
				</Box>
			</Box>
		</NativeBaseProvider>
	)
}
